import React from "react"
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize, boxShadow, width, breakpoint} from '../../util/style_property'

const ColumnOf2 = ({ children, aside }) => {

  return (
    <Wrapper>
      <MainContents>{children}</MainContents>
      <SideContents>{aside}</SideContents>
    </Wrapper>
  )
}

const Wrapper = styled.div`
  display: flex;
  width: 100%;
  height: 100%;
  padding: ${padding.l};
  margin: 0;
  justify-content: center;
  @media screen and (max-width: ${breakpoint.hd}) {
    flex-flow: column;
  }
  @media screen and (max-width: ${breakpoint.mobile_l}) {
    padding-right: 0;
    padding-left: 0;
  }
`

const MainContents = styled.main`
  display: block;
  width: 64%; 
  max-width: ${width.container};
  margin-right: ${margin.l};
  @media screen and (max-width: ${breakpoint.hd}) {
      width: 100%;
      margin-right: auto;
      margin-left: auto;
      margin-bottom: ${margin.l}
  }
  @media screen and (max-width: ${breakpoint.mobile_l}) {
    max-width: auto;
  }
`

const SideContents = styled.aside`
  display: block;
  width: 30%; 
  @media screen and (max-width: ${breakpoint.hd}) {
      width: 100%;
      max-width: ${width.container};
      margin: auto;
  }
  @media screen and (max-width: ${breakpoint.mobile_l}) {
    max-width: auto;
  }
`

export default ColumnOf2
