import React from "react"
import { graphql, useStaticQuery, Link } from 'gatsby'
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize, boxShadow, breakpoint } from '../../util/style_property'
import DropButton from './drop_button'

const TagDrops = ({tags, isButton = false}) => {
  const drops = tags.map((tag) => {
    const tagDrop = isButton ? <TagDropButton tagData={tag} /> : <TagDrop tagData={tag} />
    return (tagDrop)
  })
  return drops

}

const TagDrop = ({ tagData}) => {
  const Drop = styled.p`
    display: block;
    padding: 0 ${padding.s};
    margin: ${margin.s};
    font-size: ${textSize.m};
    font-style: bold;
    border-radius: ${borderRadius.s};
  `
  return (
    <Drop >{tagData.name}</Drop>
  )
}

const TagDropButton = ({ tagData }) => {
  return (
    <DropButton to={"/tag/" + tagData.slug}>{tagData.name}</DropButton>
  )
}

export default TagDrops