import React from "react"
import { Link } from 'gatsby'
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize, boxShadow, breakpoint } from '../../util/style_property'

const Inner = styled.div`
  width: 100%;
  height: 100%;
  padding: 0 ${padding.m};
  margin: 0;
  background-color: ${color.background_dark_gray};
  font-size: ${textSize.m};
  line-height: 2.4em;
  color: ${color.white};
  border-radius: ${borderRadius.s};
  box-shadow: ${boxShadow.l};
  transition: background-color 0.1s, color 0.1s;
`
const Wrapper = styled(Link)`
  display: block;
  padding: 1px;
  @media screen and (min-width: ${breakpoint.mobile_l}) {
    &:hover{
      padding: 2px 0 0 2px;
      text-decoration: none;
      div{
        background-color: ${color.background_light_gray};
        color: ${color.text_black};
        text-decoration: none;
        box-shadow: ${boxShadow.s};
      }
    }
  }
    @media screen and (max-width: ${breakpoint.mobile_l}) {
      &:hover div{
        color: ${color.white};
        text-decoration: none;
      }
      &:active{
        padding: 2px 0 0 2px;
        text-decoration: none;
        div{
          background-color: ${color.background_light_gray};
          color: ${color.text_black};
          box-shadow: ${boxShadow.s};
        }
      }
    }
`

const DropButton = ({ to, children}) =>{
  return(
    <Wrapper to={to} className="drop-button">
      <Inner className="inner">{children}</Inner>
    </Wrapper>
  )
}

export default DropButton