import React from "react"
import { graphql, useStaticQuery, Link } from 'gatsby'
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize } from '../../util/style_property'
import Img from "gatsby-image"

const FlexRow = styled.div`
  display:flex;
  width: 100%;
`


const ProfileCard = () => {
  const Wrapper = styled.section`
    display: block;
    width: 100%;
    padding: ${padding.m};
    background-color: ${color.white};
    border-radius: ${borderRadius.m};
  `
  return(
    <Wrapper>
      <FlexRow>
        <Icon />
        <Name />
      </FlexRow>
      <p>こんにちは、すなぎもです。<br/>
      このブログは作りかけです。よろしく。<br/>
      そもそもブログにするかも不明です。
      </p>
    </Wrapper>
  )
}

const Icon = () =>{
  const data = useStaticQuery(
    graphql`
      query{
        file(name: {eq: "profile_icon"}){
          childImageSharp{
            fixed(width: 96) {
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    `
  )

  const imageData = data.file
  const IconWrapper = styled.div`
    img{
      display: block;
      width: auto;
      height: 96px;
      border-radius: 50%;
    }
  `
  return (
    <IconWrapper>
      <Img fixed={imageData.childImageSharp.fixed} />
    </IconWrapper>
  )
}

const Name = () =>{
  const Wrapper = styled.div`
    padding: ${padding.s};
  `
  const NameH2 = styled.h2`
    color: ${color.text_black};
    font-size: ${textSize.h2};
  `
  return (
    <Wrapper>
      <NameH2>すなぎも</NameH2>
    </Wrapper>
  )
}

export default ProfileCard