import React from "react"
import PlofileCard from "./profile_card"
import TagsCard from "./tags_card"
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize,breakpoint } from '../../util/style_property'

const SideBar = () => {
  const Wrapper = styled.div`
    section{
      margin-bottom: ${margin.l}
    }
    @media screen and (max-width: ${breakpoint.mobile_l}) {
      &>section,&>div{
        border-radius: 0;
      }
    }
  `
  return(
    <Wrapper>
      <PlofileCard/>
      <TagsCard/>
    </Wrapper>
  )
}

export default SideBar