import React from "react"
import { Link } from "gatsby"
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize, boxShadow} from '../../util/style_property'
import TagDrops from './tag_drops'

const Wrapper = styled.article`
  display:block;
  width: 100%;
  /* height: 96px; */
  margin: ${margin.m} auto;
  background-color: ${color.white};
  border-radius: ${borderRadius.s};
  box-shadow: ${boxShadow.m};
  transition: background-color 0.1s;
  &:hover{
    box-shadow: ${boxShadow.s};
  }
`

const InnerLink = styled(Link)`
  display:block;
  width: 100%;
  height: 100%;
  color: inherit;
  text-decoration: none;
  &:hover{
    color: inherit;
    text-decoration: none;
  }
`

const FlexRow = styled.div`
  display:flex;
  width: 100%;
  justify-content:space-between;
`

const Thumbnail = styled.img`
  display: block;
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: ${borderRadius.xs};
`

const ThumbnailWrapper = styled.div`
  flex: 0 0 80px;
  order: 1;
  width: 30%;
  height: 100%;
  padding: ${padding.s};
`

const Body = styled.div`
  flex: 1;
  order: 2;
  height: 100%;
  padding: ${padding.m};
`

const Title = styled.h3`
  font-size:${textSize.h3};
`

const DateTime = styled.time`
  display:block;
  width: 100%;
  padding: 0;
  margin: 0;
`

const Tags = ({ tags }) => {
  const Wrapper = styled.div`
    display: flex;
    width: 100%;
    flex-wrap: wrap;
  `
  return (
    <Wrapper>
      <TagDrops tags={tags} />
    </Wrapper>
  )
}

function BlogItem({ title, date, src, slug, className}){
  console.log(date)
  const thumbnail = src ? (<ThumbnailWrapper className="thumb-nail-wrapper"><Thumbnail src={src} /></ThumbnailWrapper>) : null
  return(
    <Wrapper className={`blog-item ${className}`}>
      <InnerLink to={`/blog/${slug}`}>
        <FlexRow className = "flex-row">
          {thumbnail}
          <Body className ="blog-item-body">
            <Title>{title}</Title>
            <DateTime datetime={date}>{date}</DateTime>
          </Body>
        </FlexRow>
      </InnerLink>
    </Wrapper>
  )
}
export default BlogItem