import React from "react"
import { graphql, useStaticQuery, Link } from 'gatsby'
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize, boxShadow } from '../../util/style_property'
import TagDrops from './tag_drops'

const TagsCard = () => {
  const data = useStaticQuery(
    graphql`
      query{
        allContentfulTag(sort: {
          fields: name,
          order: ASC
        }){
          edges{
            node{
              name
              slug
            }
          }
        }
      }
    `
  )

  const Wrapper = styled.section`
    display: flex;
    width: 100%;
    padding: ${padding.m};
    background-color: ${color.white};
    border-radius: ${borderRadius.m};
    flex-wrap: wrap;

    .drop-button{
      margin: ${margin.xs};
    }
  `
  const tags = data.allContentfulTag.edges.map((edge) => { return edge.node })
  return (
    <Wrapper>
      <TagDrops tags={tags} isButton = {true}/>
    </Wrapper>
  )
}

export default TagsCard