import React from "react"
import { graphql, useStaticQuery } from 'gatsby'
import BackgroundImage from 'gatsby-background-image'
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize,breakpoint } from '../../util/style_property'

const KeyVisual = (isTop = false) => {
  const data = useStaticQuery(
    graphql`
      query{
        site{
          siteMetadata{
            title
            subtitle
          }
        }
        file(name: {eq: "key_visual"}){
          childImageSharp{
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `
  )

  const imageData = data.file.childImageSharp.fluid
  const siteMetadata = data.site.siteMetadata
  const siteTitle = isTop ? <SiteTitle>{siteMetadata.title}</SiteTitle> : <SiteTitleH2>{siteMetadata.title}</SiteTitleH2>
  return (
    <Wrapper>
      <BGI Tag="div" fluid={imageData}/>
      <Inner>
        {siteTitle}
        <SiteSubtitle>{siteMetadata.subtitle}</SiteSubtitle>
      </Inner>
    </Wrapper>
  )
}

const Wrapper = styled.div`
  position:relative;
  width: 100%;
  height: 60vh;
  padding-left: 16%;
  background-color: ${color.green};
  @media screen and (max-width: ${breakpoint.hd}) {
    height: 526px;
  }
  @media screen and (max-width: ${breakpoint.mobile_l}) {
    height: 256px;
    padding-top: 40px;
    max-width: auto;
  }
`

const BGI = styled(BackgroundImage)`
  width: 100%;
  height: 100%;
  background-position: right bottom;
  background-size: contain;
  background-color: ${color.green};
`

const Inner = styled.div`
  position: absolute;
  top: 0;
  bottom: 0;
  height: 128px;
  margin: auto;
  @media screen and (max-width: ${breakpoint.hd}) {
    top: 96px;
    bottom: auto;
    left: 114px;
  }
  @media screen and (max-width: ${breakpoint.mobile_l}) {
    top: 0;
    height: 96px;
  }
`

const SiteTitle = styled.h1`
  padding: 0;
  margin: 0;
  font-size: ${textSize.h1};
  color: ${color.text_black};
  @media screen and (max-width: ${breakpoint.hd}) {
    font-size: ${textSize.m_h1};
  }
  @media screen and (max-width: ${breakpoint.mobile_m}) {
    font-size: ${textSize.l};
  }
`

const SiteTitleH2 = SiteTitle.withComponent('h2')

const SiteSubtitle = styled.p`
  padding: 0;
  margin: 0;
  font-size: ${textSize.l};
  color: ${color.text_black};
    @media screen and (max-width: ${breakpoint.mobile_m}) {
    font-size: ${textSize.m};
  }
`

export default KeyVisual